.topbar{
    width: 100%;
    height: 87px;
    background-color: #444445;
    background-image: linear-gradient(43deg, #444445 0%, #6c6d71 46%, #909298 100%);
    position: fixed;
    // align-items: center;
    // justify-content: center;
    top: 0;
    z-index: 3;

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            display: flex;
            align-items: center;

            .logo {
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
            }

            .itemContainer {
                display: flex;
                align-items: center;
                margin-left: 30px;

                span {
                    font-size: 15px;
                    font-weight: 500;
                    text-decoration: none;
                    color: white;
                }
            }
        }

        .right{

            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;

                span{
                    width: 100%;
                    height: 3px;
                    background-color: white;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }   
    
    &.active{
        background-color: grey;
        color: white;

        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }

                &:nth-child(2) {
                opacity: 0;
                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}